#contact{
    min-height: 90vh;
    display: grid;
    grid-template-columns: 1fr 1fr;

    >section{
        background-color: $color1;
        width: 100%;
        height: 100%;

        >form{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;    

            >h2{
                text-align: center;
                font: 500 2rem $text2;
                letter-spacing: 1px;
                text-transform: uppercase;
             margin: 2rem;
            }
            >input ,>button{
                padding: 0.8rem; 
                outline: none;
                width: 60%;
                font: 400 0.9rem $text1;
                margin: 1rem;
                border: none;
            }
            >button{
                cursor: pointer;
                background-color: $color3;
                color: $color4;
                font: 600 1rem $text1;
                text-transform: uppercase;
                margin: 3rem;

                &:hover{
                    background-color: $color3_1;

                }
            }
        }
    }

    >aside{
        width: 100%;
        height: 100%;
        background-color: $color5;
        display: grid;
        place-items: center;

        >img{
            width: 70%;
            height: 70&;
            object-fit: contain;
            filter: drop-shadow(0 0 10px rgba(0,0,0,0.675)) ;
            animation: upupdowndown 0.7s  infinite linear alternate;
        }
    }
}
