#timeline{
    width: 100%;
    height: 100vh;
   
    background-color: $color1;

    .timelinebox{
        position: relative;
        width: 100%;
        padding:  $p;
        max-width: 1200PX;
        margin: 0 auto;
        display: flex;
        height: 100%;
        flex-direction: column;
        justify-content: center;



        &::after{
            content: "";
            position: absolute;
            width: 6px;
            background-color: $color5;
            top : 0;
            bottom: 0;
            left: 50%;
            margin-left: -3px;
        }

    }
 
    .timelineItem{
        padding: 0 3rem;
        position: relative;
        background-color: inherit;
         width: 50%;


&:nth-child(odd){
    text-align: right;

    &::after{
        animation: uptodown 0.3s infinite ease-in alternate;
    }
}
&:nth-child(even){
    text-align: left;
    &::after{
        animation: uptodown 0.5s infinite ease-in alternate;
    }
}

         &::after{
            content: "";
            position: absolute;
            width: 25px;
            height: 25px;
            right: -17px;
            background-color: $color1;
            border: 4px solid $color5;
            border-radius: 50%;
            top: 35px;
            z-index: 2;
         }

         >div{ 
            padding: 10px 30px;
            background-color: $color5;
            color: $color1;
            border-radius: 6px;

         }
    }
    .leftTimeline{
        left : 0;
    }
    .rightTimeline{
        left : 50%;
        &::after {
            left : -16px
        }
    }
}